$desktop-viewport: '(min-width: 768px)';

/* fonts */
$font-family: "Suisse int'l Regular", Arial, sans-serif;
$font-size: 16px;
$line-height: 1.5;

/* sizes */
$min-page-width: 320px;
$max-page-width: 1200px;
$max-content-width: 1128px;

/* breakpoints */
$phone-screen-width: 320px;
$phablet-screen-width: 600px;
$tablet-screen-width: 960px;
$desktop-screen-width: 1200px;

/* colors */
$primary: #003594;
$primary-hover: #154bad;
$primary-active: #032f7c;
$primary-50: rgba($primary, 0.5);
$primary-30: rgba($primary, 0.3);
$primary-10: rgba($primary, 0.1);

$dark: #111820;
$dark-gray: #dfe3eb;
$dark-hover: #1f2c3b;
$dark-active: #000;
$dark-75: rgba($dark, 0.75); // TODO такого цвета пока нет в UI Library, но Маша обещала добавить
$dark-50: rgba($dark, 0.5);
$dark-30: rgba($dark, 0.3);
$dark-10: rgba($dark, 0.1);

$blue-gray: #5e6e87;
$blue-gray-80: rgba($blue-gray, 0.8);
$blue-gray-50: rgba($blue-gray, 0.5);

$light: #fff;
$light-hover: rgba($light, 0.9);
$light-active: rgba($light, 0.7);
$light-70: rgba($light, 0.7);
$light-50: rgba($light, 0.5);
$light-30: rgba($light, 0.3);
$light-10: rgba($light, 0.1);

$gray: #ebeef3;
$gray-light: #f6f8fc;
$gray-dark: #dfe3eb;
$gray-heavy: #bcc1cc;

$tariff-minimum: #edf4ff;
$tariff-optimum: #d3e0f5;
$tariff-premium: #b2cced;
$tariff-business: $dark;

$success: #008c5f;

$green: #008c5f;
$yellow: #008c5f;
$yellow-light: #ffc332;
$red: #dc2328;
$error: $red;

$error-30: rgba($error, 0.3); // TODO такого цвета пока нет в UI Library, обещают добавить

$facebook: #3b5998;
$facebook-hover: #3460bb;
$vkontakte: #597da3;
$vkontakte-hover: #508bc9;
$telegram: #08c;
$telegram-hover: #06a1ee;
